import React, { useRef } from 'react';
import { View, Text } from 'react-native';

import Box from '@mui/material/Box';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Toolbar from '@mui/material/Toolbar';
import Checkbox from '@mui/material/Checkbox';
import TextField from '@mui/material/TextField';

import { api_login } from '@app-api';
import { parseDataToObject } from '@app-utils';

import ManualButton from '@app-component/ManualInvoice';

import styles from './styles';

const InfoUser = ({ userInfo, updateUserInfo }) => {
  const _localStorageKey = '@loginInfo';
  const _userInfoStorage = localStorage.getItem(_localStorageKey);
  const _userInfo = parseDataToObject(_userInfoStorage);

  const username = useRef(_userInfo?.username || '');
  const password = useRef(_userInfo?.password || '');
  const isRemember = useRef(!!_userInfoStorage);

  const _onChecked = () => isRemember.current = !isRemember.current;

  const _onChangeUserName = event => username.current = event.target.value;

  const _onChangePassword = event => password.current = event.target.value;

  const _login = async () => {
    if ((!!username.current.length && !!password.current.length)) {
      const result = await api_login(username.current, password.current);
      if (!!result?.data?.token) {

        if (!!isRemember.current) {
          const _loginInfo = { username: username.current, password: password.current };
          localStorage.setItem(_localStorageKey, JSON.stringify(_loginInfo));
        }

        if (!isRemember.current) localStorage.removeItem(_localStorageKey);

        updateUserInfo(result?.data);
      }
      else {
        alert(result?.message || 'Something is wrong :))');
      }
    }
  }

  return (
    <Box>
      <AppBar position="static">
        <Toolbar>
          <TextField
            size='small'
            placeholder='Tên đăng nhập'
            InputProps={{ style: styles.input }}
            defaultValue={username.current}

            onChange={_onChangeUserName}
          />

          <TextField
            size='small'
            type='password'
            placeholder='Mật khẩu'
            InputProps={{ style: styles.input }}
            defaultValue={password.current}

            onChange={_onChangePassword}
          />

          <View style={styles.row}>
            <Checkbox sx={styles.checkBox} defaultChecked={isRemember.current} onClick={_onChecked} />
            <Text style={styles.remember}>Nhớ tài khoản</Text>
          </View>

          <View style={styles.button}>
            <ManualButton />
            <Button color="inherit" onClick={_login}>Login</Button>
          </View>
        </Toolbar>
      </AppBar>
    </Box>
  )

}

export default InfoUser;