import React, { useState, useEffect, useRef } from 'react';
import { View, Text } from 'react-native';

import ReactToPrint from 'react-to-print';

import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';

import Invoice from '@app-component/Invoice';

import styles from './styles';

const PrintMultiple = ({ orders, data, selectAll }) => {
  const _disabled = !orders?.length;
  const _pointerEvents = !!_disabled ? 'none' : 'auto';

  const [isSelectAll, setSelectAll] = useState(false);

  const ref = useRef();

  const _onChange = () => selectAll();

  useEffect(() => {
    if (data?.length === orders?.length) setSelectAll(true);
    if (data?.length !== orders?.length) setSelectAll(false);
  }, [JSON.stringify(data), JSON.stringify(orders)]);

  const _renderItem = (item, index) => {
    const billCode = item?.billCode;
    const codValue = item?.codValue;
    const sender = `${item.addresseeName} - ${item.addresseePhone}`;
    const address = `${item.shippingAddress} - ${item.addresseeSubCountyName} - ${item.addresseeCountyName} - ${item.addresseeProvName}`;

    const params = { billCode, codValue, sender, address };

    return <View key={index}><Invoice params={params} /></View>;
  }

  return (
    <View>
      <View style={styles.container}>
        <View style={styles.selectView}>
          <Checkbox color='warning' checked={isSelectAll} onChange={_onChange} />
          <Text>Chọn Tất Cả</Text>
        </View>
        <View pointerEvents={_pointerEvents}>
          <ReactToPrint content={() => ref.current} trigger={() =>
            <a href="#" style={styles.print}>
              <Button variant="contained" disabled={_disabled}>In Đơn Hàng Được Chọn</Button>
            </a>}
          />
        </View>
      </View>
      <View style={styles.orders}><div ref={ref}>{orders?.map?.(_renderItem)}</div></View>
    </View>
  )

}

export default PrintMultiple;
