import React, { useState, useEffect, useMemo } from 'react';
import { View, Text } from 'react-native';

import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';

import { api_orders, api_printData } from '@app-api';

import { runAfterInteractions } from '@app-utils';

import ListOrderItem from '@app-component/ListOrderItem';
import PrintMultiple from '@app-component/PrintMultiple';

import styles from './styles';

const ListOrder = ({ userInfo }) => {
  const authorization = userInfo.token;

  const [data, setData] = useState(null);
  const [orders, setOrders] = useState([]);
  const [refreshing, setRefreshing] = useState(false);

  const _loadData = async () => {
    const list = await api_orders(authorization);
    const _list = list?.data?.list || [];

    let _result = [];
    if (!!_list?.[0]) {
      const _orderIds = _list.map(iItem => iItem.orderId);
      _result = await api_printData(authorization, _orderIds);
    }
    let dataArr = _result?.data || [];
    for (var i = 0; i < dataArr.length; i++) {
      const or = _list.find(o => o.orderId === dataArr[i].orderId);
      dataArr[i].addresseeName = or?.addresseeName || dataArr[i].addresseeName;
    }
    setData([...(dataArr || [])]);
    if (!!refreshing) setRefreshing(false);
  }

  const _selectItem = (item, isAllowAdd) => {
    const _orders = [...orders];
    if (!!isAllowAdd) _orders.push(item);
    if (!isAllowAdd) {
      const _index = _orders.findIndex(el => el.orderId == item.orderId);
      if (_index > -1) _orders.splice(_index, 1);
    }
    setOrders(_orders);
  }

  const _selectAll = () => {
    const isAll = (data?.length !== orders?.length);
    if (!isAll) setOrders([]);
    if (!!isAll) setOrders([...data]);
  }

  const _onRefresh = () => setRefreshing(true);

  useEffect(() => {
    if (!!authorization && !!data) setData(null);
  }, [authorization]);

  useEffect(() => {
    if (!data || !!refreshing) runAfterInteractions(_loadData, 1000);
  }, [data, refreshing]);

  const memoPrintMultiple = useMemo(() => <PrintMultiple orders={orders} data={data} selectAll={_selectAll} />, [orders, data]);

  const _renderItem = item => <ListOrderItem item={item} orders={orders} selectItem={_selectItem} key={item?.orderId} />;

  const _renderLoading = () => <View style={styles.loading}><CircularProgress /></View>;

  const _renderOrders = () => (
    <View style={styles.view}>
      {data.map(_renderItem)}
    </View>
  )

  const _renderEmpty = () => {
    return (
      <View style={styles.emptyView}>
        <Text style={styles.emptyTitle}>Hiện Không Có Đơn Hàng</Text>
        <Button style={styles.button} variant="contained" onClick={_onRefresh}>
          {!refreshing ? <Text style={styles.buttonText}>Làm mới</Text> : <CircularProgress size={24} color='inherit' />}
        </Button>
      </View>
    )
  }

  return (
    <View style={styles.container}>
      {!!data?.[0] && memoPrintMultiple}

      {!data && _renderLoading()}

      {!!data?.[0] && _renderOrders()}

      {!!data && !data?.[0] && _renderEmpty()}
    </View>
  )

}

export default ListOrder;