import { InteractionManager } from "react-native";

export const runAfterInteractions = (fn, time = 0, { cancellable } = {}) => {
  let timer;
  const run = InteractionManager.runAfterInteractions(() => {
    timer = setTimeout(() => {
      if (!!fn && (typeof (fn) === 'function')) fn();
      clearTimeout(timer);
    }, time);
  });
  if (cancellable)
    return () => {
      clearTimeout(timer);
      run.cancel();
    }
}

export function parseDataToObject(data) {
  if (!data) return {};
  try {
    return JSON.parse(data);
  }
  catch (error) {
    return {};
  }
}
