import React, { useState, useEffect, useRef } from 'react';
import { View, Text } from 'react-native';

import ReactToPrint from 'react-to-print';

import { runAfterInteractions } from '@app-utils';

import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import TextField from '@mui/material/TextField';

import Invoice from '@app-component/Invoice';

import styles from './styles';

const ManualButton = () => {
  const [isOpen, setOpen] = useState(false);
  const [fontSize, setFontSize] = useState(16);
  const [data, setData] = useState({ name: '', phone: '', address: '', billCode: '', codValue: '' });

  const ref = useRef(null);

  const _onOpen = () => setOpen(true);

  const _onClose = () => {
    window.history.pushState({}, document.title, "/" + '');

    setOpen(false);
    setData({ name: '', phone: '', address: '', billCode: '', codValue: '' });
  }

  const _onCopy = () => {
    const hostName = window.location.hostname;
    const url = hostName + "/?action=print&receiver=" + encodeURIComponent(data.name) + "&phone=" + encodeURIComponent(data.phone) + "&addr="
      + encodeURIComponent(data.address) + "&cod=" + encodeURIComponent(data.codValue);

      navigator.clipboard.writeText(url);
  }

  const _sender = event => setData({ ...data, name: event.target.value });

  const _phone = event => setData({ ...data, phone: event.target.value });

  const _address = event => setData({ ...data, address: event.target.value });

  const _billCode = event => setData({ ...data, billCode: event.target.value });

  const _codValue = event => setData({ ...data, codValue: event.target.value });

  const _fontSize = event => setFontSize(+event.target.value);

  const _getPrint = _ref => { ref.current = _ref; };

  const _onLoadDataQuery = () => {
    const params = new URLSearchParams(decodeURIComponent(location.search || ''));
    const queryParams = Array.from(params.keys()).reduce((a, b) => ({ ...a, [b]: params.get(b) }), {});

    if (queryParams?.action === 'print') {
      if (!!queryParams?.receiver || !!queryParams?.phone || !!queryParams?.addr || !!queryParams?.bill || !!queryParams?.cod) {
        setOpen(true);
        setData({ name: (queryParams?.receiver || ''), phone: (queryParams?.phone || ''), address: (queryParams?.addr || ''), billCode: (queryParams?.bill || ''), codValue: (queryParams?.cod || '') })
      }
    }
  }

  useEffect(() => {
    runAfterInteractions(_onLoadDataQuery);
  }, []);

  const billCode = data?.billCode;
  const codValue = data?.codValue;
  const address = `${data.address}`;
  const sender = `${data.name} - ${data.phone}`;
  const params = { billCode, codValue, sender, address };

  return (
    <View style={styles.container}>
      <Button style={styles.button} variant="outlined" onClick={_onOpen}>Tạo hoá đơn</Button>
      <Dialog open={isOpen} sx={styles.dialogView}>
        <View style={styles.dialog}>
          <View style={styles.form}>
            <View>
              <View style={styles.row}>
                <TextField style={styles.view} value={data.name} label='Người nhận' color='primary' onChange={_sender} />
              </View>
              <View style={styles.row}>
                <TextField style={styles.view} value={data.phone} label='SĐT' color='primary' onChange={_phone} />
              </View>
              <View style={styles.row}>
                <TextField style={styles.view} value={data.address} label='Địa chỉ' color='primary' onChange={_address} />
              </View>
              {/* <View style={styles.row}>
                <TextField style={styles.view} label='Mã Barcode' color='primary' onChange={_billCode} />
              </View> */}
              <View style={styles.row}>
                <TextField style={styles.view} value={data.codValue} label='Giá tiền (COD)' color='primary' onChange={_codValue} />
              </View>
              <View style={styles.row}>
                <TextField value={fontSize} label='Cỡ chữ' color='primary' onChange={_fontSize} />
              </View>
            </View>
            <View style={styles.center} />
            <Invoice params={params} fontSize={fontSize} getPrint={_getPrint} />
          </View>

          <View style={styles.bottom}>
            <Button variant='contained' color='warning' style={styles.cancel} onClick={_onCopy}>
              <Text style={styles.buttonText}>Copy</Text>
            </Button>
            <View style={styles.center} />
            <Button variant='contained' color='warning' style={styles.cancel} onClick={_onClose}>
              <Text style={styles.buttonText}>Huỷ</Text>
            </Button>
            <View style={styles.center} />
            <ReactToPrint content={() => ref.current} trigger={() =>
              <a href="#" style={styles.print}>
                <Button variant="contained">
                  <Text style={styles.buttonText}>Print</Text>
                </Button>
              </a>}
            />
          </View>
        </View>
      </Dialog>
    </View>
  )

}
export default ManualButton;
