import React, { useEffect, useRef } from 'react';
import { View, Text, Image } from 'react-native';

import numeral from 'numeral';
import Barcode from 'react-barcode';

import logo from '../../../assets/logo.jpg';

import styles from './styles';

const Invoice = ({ params, fontSize, getPrint }) => {
  const css = `
    @media print {
      @page {
        size: ${styles.container.width} ${styles.container.height};
      }
    }
  `;

  const ref = useRef();

  useEffect(() => getPrint?.(ref.current), []);

  const addressStyle = [styles.invoiceText, !!fontSize && { fontSize, lineHeight: (fontSize + (fontSize / 2)) }];

  return (
    <div ref={ref}>
      <style>{css}</style>
      <View style={styles.container}>
        <View style={styles.logo}>
          <Image source={logo} style={styles.imgLogo} />
        </View>
        <View style={styles.invoiceView}>
          <Text style={styles.invoiceText}>Khoai Tây Shop - 0907534545</Text>
          <Text style={styles.invoiceText}>370 Nguyễn Đình Chiểu, Phường 4, Quận 3</Text>
        </View>

        <View style={styles.line} />

        <View style={styles.invoiceAddress}>
          <Text style={addressStyle}>
            <Text style={styles.thick}>Người nhận: </Text> {params?.sender}
          </Text>
          <Text style={addressStyle} numberOfLines={3}>{params?.address}</Text>
        </View>

        {(!!params?.billCode || !!params?.codValue) && <View style={styles.line} />}

        {(!!params?.billCode || !!params?.codValue) &&
          <View style={styles.barCode}>
            {!!params?.billCode &&
              <View style={styles.barCodeDetail} >
                <Barcode value={params?.billCode} />
              </View>
            }
            {!!params?.codValue &&
              <View style={styles.CODView}>
                <Text style={styles.COD}>COD: {numeral(params?.codValue).format('0,0')} đ</Text>
              </View>
            }
          </View>
        }

        <View style={styles.line} />

        <View style={styles.footer}>
          <Text style={styles.thick}>
            Shipper ko gọi được khách xin hãy liên lạc cho shop, đừng tự hoàn hàng nhé. Cám ơn rất nhiều ạ ❤️
          </Text>
        </View>
      </View>
    </div>
  )

};

export default Invoice;
