const DOMAIN = 'https://v9-cc.800best.com'

export const api_login = async (account, password) => {
  const uri = `${DOMAIN}/uc/account/login`;

  const result = await fetch(uri,
    {
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ account, password, phone: "", mode: 0, "graphicVerifyCodeGuid": null }),
      method: 'POST'
    }
  ).then(response => response.json());

  return result;
}

export const api_orders = async authorization => {
  const uri = `${DOMAIN}/express-cc/express/myExpressList`;

  const result = await fetch(uri,
    {
      headers: { 'Content-Type': 'application/json', 'accept': 'application/json', authorization },
      body: JSON.stringify({
        "currentPage": 1,
        "type": 0,
        "status": "00,01",
        "condition": "",
        "dataSource": "OFFICIAL WEB",
        "isProblem": 1,
        "problemDetail": 0
      }),
      method: 'POST'
    }
  ).then(response => response.json());

  return result;
}

export const api_printData = async (authorization, orderId) => {
  const uri = `${DOMAIN}/express-cc/order/printData`;

  const result = await fetch(uri,
    {
      headers: { 'Content-Type': 'application/json', 'accept': 'application/json', authorization },
      body: JSON.stringify({
        txLogisticIds: [...orderId],
        crossBorder: false
      }),
      method: 'POST'
    }
  ).then(response => response.json());

  return result;
}