import React, { useState, useMemo } from 'react';
import { View } from 'react-native';

import InfoUser from '@app-component/InfoUser';
import ListOrder from '@app-component/ListOrder';

import styles from './styles';

const App = () => {
  const [userInfo, setUserInfo] = useState(null);

  const _updateUserInfo = info => setUserInfo(info);

  const memoInfoUser = useMemo(() => <InfoUser userInfo={userInfo} updateUserInfo={_updateUserInfo} />, [userInfo]);
  const memoListOrder = useMemo(() => <ListOrder userInfo={userInfo} />, [userInfo]);

  return (
    <View style={styles.container}>
      {memoInfoUser}
      {!!userInfo?.token &&
        <View style={styles.view}>
          {memoListOrder}
        </View>
      }
    </View>
  )

}

export default App;
