import React, { useRef } from 'react';
import { View, Text } from 'react-native';

import ReactToPrint from 'react-to-print';

import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';

import Invoice from '@app-component/Invoice';

import styles from './styles';

const ListOrderItem = ({ item, orders, selectItem }) => {
  const billCode = item?.billCode;
  const codValue = item?.codValue;
  const sender = `${item.addresseeName} - ${item.addresseePhone}`;
  const address = `${item.shippingAddress} - ${item.addresseeSubCountyName} - ${item.addresseeCountyName} - ${item.addresseeProvName}`;

  const params = { billCode, codValue, sender, address };

  const isChecked = !!(orders?.findIndex(el => el.orderId === item.orderId) > -1);

  const ref = useRef();

  const _getPrint = _ref => {
    ref.current = _ref;
  }

  const _onChange = event => selectItem(item, event.target.checked);

  return (
    <View>
      <Paper style={styles.container} elevation={3}>
        <View style={styles.titleView}>
          <Text style={styles.text}>Mã vận đơn: {item.orderId}</Text>
          <Checkbox color='warning' style={styles.checkBox} checked={isChecked} onChange={_onChange} />
        </View>

        <View style={styles.infoView}>

          <Invoice params={params} getPrint={_getPrint} />

          <ReactToPrint content={() => ref.current} trigger={() =>
            <a href="#" style={styles.print}>
              <Button variant="contained">Print</Button>
            </a>}
          />

        </View>
      </Paper>
    </View>
  )

}

export default ListOrderItem;
